<template>
    <div>
        <el-dialog title="" :visible.sync="dialogVisible" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
            <el-form :model="guding_form" ref="guding_form" label-width="150px" :rules="guding_rules">
                <div style="display: flex;flex-wrap: wrap">
                    <div style="width: 450px;">
                        <el-form-item label="资产名称：" prop="assetName">
                            <el-input v-model="guding_form.assetName" auto-complete="off"></el-input>
                        </el-form-item>
                    </div>
                    <div style="width: 450px;">
                        <el-form-item label="资产类别：" prop="assetClassify">
                            <el-select v-model="guding_form.assetClassify" placeholder="请选择" style="width: 100%;">
                                <el-option label="1-房屋、建筑物" value="1"></el-option>
                                <el-option label="2-机器机械生产设备" value="2"></el-option>
                                <el-option label="3-器具、工具、家具" value="3"></el-option>
                                <el-option label="4-运输工具" value="4"></el-option>
                                <el-option label="5-电子设备" value="5"></el-option>
                                <el-option label="6-其他固定资产" value="6"></el-option>
                            </el-select>
                        </el-form-item>
                    </div>
                    <div style="width: 450px;">
                        <el-form-item label="开始使用日期：" prop="startDate">
                            <el-date-picker style="width: 100%;" value-format="yyyy-MM-dd" type="date" placeholder="默认当前日期" v-model="guding_form.startDate"></el-date-picker>
                        </el-form-item>
                    </div>
                    <div style="width: 450px;">
                        <el-form-item label="选择科目：" prop="assetSubjects">
                            <el-select v-model="guding_form.assetSubjects" placeholder="请选择" style="width: 100%;">
                                <el-option :key="item.subjects_name" v-for="item in subject" :label="item.subjects_name" :value="item.subjects_name"></el-option>
                            </el-select>
                        </el-form-item>
                    </div>
                    <div style="width: 450px;">
                        <el-form-item label="资产原值：" prop="zMoeny">
                            <el-input v-model.number="guding_form.zMoeny" auto-complete="off"></el-input>
                        </el-form-item>
                    </div>
                    <div style="width: 450px;">
                        <el-form-item label="企业类别：" prop="flag">
                            <el-select v-model="guding_form.flag" placeholder="请选择" style="width: 100%;">
                                <el-option label="内资" value="0"></el-option>
                                <el-option label="外资" value="1"></el-option>
                            </el-select>
                        </el-form-item>
                    </div>
                    <div style="width: 450px;">
                        <el-col :span="22">
                            <el-form-item label="残值率：" prop="residualValue">
                                <el-input v-model.number="guding_form.residualValue" auto-complete="off" style="width: 100%;"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="1" :offset="1">
                            <el-form-item label="%" label-width="0px">
                            </el-form-item>
                        </el-col>



                    </div>
                    <div style="width: 450px;">
                        <el-form-item label="预计使用月份：" prop="setMonth">
                            <el-input v-model.number="guding_form.setMonth" auto-complete="off"></el-input>
                        </el-form-item>
                    </div>
                    <div style="width: 450px;" v-if="true">
                        <el-form-item label="已折旧月份：" prop="getMonth">
                            <el-input v-model.number="guding_form.getMonth" auto-complete="off"></el-input>
                        </el-form-item>
                    </div>
                    <div style="width: 450px;" v-if="true">
                        <el-form-item label="剩余使用月份：" prop="shengyu">
                            <el-input disabled v-model="guding_form.shengyu" auto-complete="off"></el-input>
                        </el-form-item>
                    </div>
                    <div style="width: 450px;">
                        <el-form-item label="累计折旧：" prop="setZMoney">
                            <el-input v-model.number="guding_form.setZMoney" auto-complete="off"></el-input>
                        </el-form-item>
                    </div>
                    <div style="width: 450px;">
                        <el-form-item label="减值准备：" prop="getZMoney">
                            <el-input v-model.number="guding_form.getZMoney" auto-complete="off"></el-input>
                        </el-form-item>
                    </div>
                    <div style="width: 450px;" v-if="true" >
                        <el-form-item label="每月折旧额：" prop="meiyue">
                            <el-input disabled v-model="guding_form.meiyue" auto-complete="off"></el-input>
                        </el-form-item>
                    </div>
                </div>
            </el-form>
            <span slot="footer">
                <el-button @click="guding_cancel">取 消</el-button>
                <el-button type="primary" @click="guding_ok">确 定</el-button>
             </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "add_gudingzichan",
        watch:{
            'guding_form': {
                handler(newName, oldName) {
                    // 剩余 = 预计 - 折旧
                    //这俩个都为数字且不为空时
                    if(!isNaN(Number(this.guding_form.getMonth)) && !isNaN(Number(this.guding_form.setMonth)) && this.guding_form.getMonth !=="" && this.guding_form.setMonth !==""){

                        this.guding_form.shengyu = this.guding_form.setMonth - this.guding_form.getMonth

                        this.$refs.guding_form.validateField('shengyu');
                        //如果剩余有了才算这个
                        if(this.guding_form.zMoeny !== "" && this.guding_form.residualValue !== "" && !isNaN(Number(this.guding_form.zMoeny)) && !isNaN(Number(this.guding_form.residualValue))){
                            this.guding_form.meiyue = (this.guding_form.zMoeny * (1 - this.guding_form.residualValue*0.01) / this.guding_form.shengyu).toFixed(2)

                            this.$refs.guding_form.validateField('meiyue');

                        }else{
                            this.guding_form.meiyue = ""
                        }


                    }else{
                        this.guding_form.shengyu = ""
                        this.guding_form.meiyue = ""
                        this.$refs.guding_form.validateField('shengyu');
                        this.$refs.guding_form.validateField('meiyue');

                    }
                },
                deep:true
            },
            // // 预计使用月份
            // 'guding_form.setMonth': {
            //     handler(newName, oldName) {
            //         this.guding_form.shengyu = this.guding_form.setMonth - this.guding_form.getMonth
            //     },
            // },
            // //已折旧月份
            // 'guding_form.getMonth': {
            //     handler(newName, oldName) {
            //         this.guding_form.shengyu = this.guding_form.setMonth - this.guding_form.getMonth
            //     },
            // },
            // //资产原值
            // 'guding_form.zMoeny': {
            //     handler(newName, oldName) {
            //
            //         this.guding_form.meiyue = this.guding_form.zMoeny * (1 - this.guding_form.residualValue*0.01) / this.guding_form.shengyu
            //
            //     },
            // },
            // // 剩余使用月份
            // 'guding_form.shengyu': {
            //     handler(newName, oldName) {
            //
            //         this.guding_form.meiyue = this.guding_form.zMoeny * (1 - this.guding_form.residualValue*0.01) / this.guding_form.shengyu
            //
            //     },
            // },
            //残值率
            'guding_form.residualValue': {
                handler(newName, oldName) {
                    if(newName > 100){
                        this._message(2,"残值率不得大于100")
                    }
                },
            },
            // 资产类别
            'guding_form.assetClassify':{
                handler(newName, oldName) {
                    if(this.guding_form.assetClassify == 1 || this.guding_form.flag == 1){
                        this.guding_form.residualValue = 10
                    }else{
                        this.guding_form.residualValue = 5
                    }
                },
            },
            //企业类别
            'guding_form.flag': {
                handler(newName, oldName) {
                    if(this.guding_form.assetClassify == 1 || this.guding_form.flag == 1){
                        this.guding_form.residualValue = 10
                    }else{
                        this.guding_form.residualValue = 5
                    }
                }
            }
        },
        mounted(){
            this._ajax('userAccounts/selectSubjectsDictionary1',{
                uuid:this.uuid,
                accountId:this.accountId,
                parentId:1601,
                name:''
            }, msg => {
                this.subject = msg
                // this._message(1,"科目成功")
            })
        },
        data(){
            var shengyu = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入预计使用月份和已折旧月份'));
                } else {
                    //如果输入的都是数字
                    // if (!isNaN(Number(this.guding_form.getMonth)) && !isNaN(Number(this.guding_form.setMonth)) && this.guding_form.getMonth !=="" && this.guding_form.setMonth !=="") {
                    if(this.guding_form.setMonth < this.guding_form.getMonth){
                        callback(new Error('已折旧月份不得大于预计使用月份'))
                    }else{
                        callback();
                    }
                    // }
                }
            };
            var meiyue = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入预计使用月份,已折旧月份,资产原值和残值率'));
                } else {
                    //     //如果输入的都是数字
                    //     if (!isNaN(Number(this.guding_form.getMonth)) && !isNaN(Number(this.guding_form.setMonth)) && this.guding_form.getMonth !=="" && this.guding_form.setMonth !=="") {
                    if(this.guding_form.meiyue < 0){
                        callback(new Error("每月折旧额不得小于0"))
                    }else{
                        callback();
                    }
                    // }
                }
            };
            return{
                subject:[],
                dialogVisible:true,
                guding_form:{
                    assetName:'',
                    assetClassify:"",
                    startDate:new Date().getFullYear() + '-' + (Array(2).join(0) + (Number(new Date().getMonth())+1)).slice(-2) + '-' + (Array(2).join(0) + (Number(new Date().getDate()))).slice(-2),
                    // 科目
                    assetSubjects:"",
                    // 资产原值
                    zMoeny:"",
                    flag:'',
                    // 残值率
                    residualValue:5,

                    //预计使用月份
                    setMonth:'',
                    //已折旧月份
                    getMonth:0,

                    // 累计折旧
                    setZMoney:0,
                    // 减值准备
                    getZMoney:0,

                    shengyu:'',
                    meiyue:'',
                },
                guding_rules: {
                    meiyue:[
                        { required: true,validator: meiyue, trigger: 'blur' }
                    ],
                    shengyu:[
                        { required: true,validator: shengyu, trigger: 'blur' }
                    ],
                    assetName: [
                        { required: true, message: '请输入资产名称', trigger: 'blur' },
                    ],
                    assetClassify: [
                        { required: true, message: '请选择资产类别', trigger: 'change' },
                    ],
                    startDate: [
                        { required: true, message: '请选择开始使用日期', trigger: 'change' },
                    ],
                    assetSubjects: [
                        { required: true, message: '请选择科目', trigger: 'change' },
                    ],
                    zMoeny: [
                        { required: true, message: '请输入资产原值', trigger: 'blur' },
                        { type: 'number', message: '必须为数字值'}
                    ],
                    flag: [
                        { required: true, message: '请选择企业类别', trigger: 'change' },
                    ],
                    residualValue: [
                        { required: true, message: '请输入残值率', trigger: 'blur' },
                        { type: 'number', message: '必须为数字值'}
                    ],
                    setMonth: [
                        { required: true, message: '请输入', trigger: 'blur' },
                        { type: 'number', message: '必须为数字值'}
                    ],
                    getMonth: [
                        { required: true, message: '请输入', trigger: 'blur' },
                        { type: 'number', message: '必须为数字值'}
                    ],
                    setZMoney: [
                        { required: true, message: '请输入累计折旧', trigger: 'blur' },
                        { type: 'number', message: '必须为数字值'}
                    ],
                    getZMoney: [
                        { required: true, message: '请输入减值准备', trigger: 'blur' },
                        { type: 'number', message: '必须为数字值'}
                    ],
                },

            }
        },
        methods:{
            //固定资产的取消键
            guding_cancel(){
                this.$refs['guding_form'].resetFields();
                this.$emit("gudingzichan_jieshou",0)
            },
            // 固定资产的确认键
            guding_ok(){
                this.$refs["guding_form"].validate((valid) => {
                    if (valid) {
                        this.$emit("gudingzichan_jieshou",this.guding_form)
                        this.$store.commit('sets',this.guding_form)

                        // this.save(this.save_fangshi,"guding")
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
        }

    }
</script>

<style scoped>

</style>


