<template>
    <div>
        <el-form :model="user" ref="user">
            <el-table :data="user.tableData" style="width: 100%">
                <el-table-column
                        v-if="pingzheng"
                        prop=""
                        label="科目"
                        width="">
                    <template slot-scope="scope" >
                        <el-form-item style="margin-bottom: 0" label="" :prop="'tableData.' + scope.$index +'.name'" :rules="[{ required: true, message: '请输入科目', trigger: 'change' }]">
                            <el-select filterable v-model="scope.row.name" placeholder="请选择" style="width: 100%;" @change="change_kemu">
                                <el-option :key="item.id" v-for="(item,index) in list" :label="item.new_subjects_name" :value="item.subjects_name+','+item.subjects_number"></el-option>
                            </el-select>
                        </el-form-item>
                    </template>
                </el-table-column>
                <el-table-column
                        v-if="jiezhuan"
                        prop=""
                        label="科目"
                        width="">
                    <template slot-scope="scope" >
                        <el-form-item style="margin-bottom: 0" label="" :prop="'tableData.' + scope.$index +'.name'" :rules="[{ required: true, message: '请输入科目', trigger: 'change' }]">
                            <el-select filterable v-model="scope.row.name" placeholder="请选择" style="width: 100%;">
                                <el-option :key="item.id" v-for="(item,index) in list" :label="item.name" :value="item.name+','+item.code+','+item.type1"></el-option>
                            </el-select>
                        </el-form-item>
                    </template>
                </el-table-column>
                <el-table-column
                        prop=""
                        label="摘要"
                        width="">
                    <template slot-scope="scope">
                        <el-form-item style="margin-bottom: 0" label="" :prop="'tableData.' + scope.$index +'.zhaiyao'" :rules="[{ required: true, message: '请输入摘要', trigger: 'blur' }]">
                            <el-input v-model="scope.row.zhaiyao" placeholder=""></el-input>
                        </el-form-item>
                    </template>
                </el-table-column>
                <el-table-column
                        prop=""
                        label="借方金额"
                        width="">
                    <template slot-scope="scope">
                        <el-input @input="jie_change(scope.row)" :disabled="scope.row.jie_show" v-model="scope.row.jie" placeholder=""></el-input>
                    </template>
                </el-table-column>
                <el-table-column
                        prop=""
                        label="贷方金额"
                        width="">
                    <template slot-scope="scope">
                        <el-input @input="dai_change(scope.row)" :disabled="scope.row.dai_show" v-model="scope.row.dai" placeholder=""></el-input>
                    </template>
                </el-table-column>
                <el-table-column
                        fixed="right"
                        label="操作"
                        width="150">
                    <template slot-scope="scope">
                        <el-button @click="add" type="text" size="small">增加</el-button>
                        <el-button v-if="user.tableData.length !=2" @click="del(scope.row.index)" type="text" size="small">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div style="color: #909399;margin-top: 10px;margin-left: 10px">借方合计:{{heji}}</div>
            <div style="color: #909399;margin-top: 10px;margin-left: 10px">贷方合计:{{heji1}}</div>
        </el-form>

        <gudingzichan v-if="gudingzichan_kaiguan" @gudingzichan_jieshou="gudingzichan_jieshou"></gudingzichan>

    </div>
</template>

<script>
    import mix from "@/mixins/mixins"
    import gudingzichan from "@/components/yuchunhui/add_gudingzichan"


    export default {
        name: "add_pingzheng",
        components:{
            gudingzichan
        },
        props:["yaoxinxi","fanxian","qimo_editor_fanxian","type","close_guding"],
        mixins:[mix],
        data(){
            return {
                type1:"",
                // 控制固定资产的开关
                gudingzichan_kaiguan:false,

                // 暂存凭证信息
                data:[],

                // 判断是凭证那块进来的还是结转进来的
                pingzheng:false,
                jiezhuan:false,

                heji:0,
                heji1:0,
                index:1,
                list:[],
                user:{
                    tableData: [{name:'',zhaiyao:'',jie:"",dai:"",jie_show:false,dai_show:false,index:0},
                        {name:'',zhaiyao:'',jie:"",dai:"",jie_show:false,dai_show:false,index:1}],
                },
            }
        },
        watch:{
            //爸爸让我关闭固定资产这个孙子
            close_guding(){
                this.gudingzichan_kaiguan = false
            },

            //实现先有list再返显信息
            list(x){
                if(this.data.length!=0){
                    this.user.tableData = this.data
                }
            },
            //如果返显先走了,先把值存起来,然后list有的时候才会赋值
            //期末结转的修改返显
            qimo_editor_fanxian(x){
                this.data = x
                if(this.list.length!=0){
                    this.user.tableData = x
                }
            },
            //修改凭证返显信息
            fanxian(x){
                this.data = x
                if(this.list.length!=0){
                    this.user.tableData = x
                }
            },

            // 爸爸和我要信息我先检查一下再给他
            yaoxinxi(x){
                if(x){
                    this.jiancha()
                }
            },
            'user.tableData': {
                handler(newName, oldName) {
                    // 判断信息是否填写完整
                    let jie_num = 0
                    let dai_num = 0

                    this.user.tableData.forEach(item=>{
                        // 计算总数
                        if(item.jie != ""){
                            jie_num += Number(item.jie)
                        }
                        if(item.dai != ""){
                            dai_num += Number(item.dai)
                        }
                    })
                    this.heji = jie_num
                    this.heji1 = dai_num

                },
                deep:true
            },
        },

        mounted(){
            this.getStorage()
            this.get_jiezhuan_date()
            if(this.type == "add_pingzheng" || this.type == "editor_pingzheng"){
                this.pingzheng = true
                this.jiezhuan = false
            }else if(this.type == "jiezhuan" || this.type == "shengcheng" || this.type == "editor"){
                this.pingzheng = false
                this.jiezhuan = true
            }
            //结账用的
            if(this.type == "jiezhuan"){
                this._ajax('userHomePage/jieZhuanSunYiTemplate',{
                    uuid:this.uuid,
                    workAccountId:this.accountId,
                    timeMonth:this.jiezhuan_date
                }, msg => {
                    this.list = msg
                    // this._message(1,"科目成功")
                })
                //添加或者修改凭证用的
            }else if(this.type == "add_pingzheng" || this.type == "editor_pingzheng"){
                this._ajax('userAccounts/selectClassName',{
                    uuid:this.uuid,
                    workAccountId:this.accountId,
                }, msg => {
                    this.list = msg
                    // this._message(1,"会计科目成功")
                })
                // 期末结转来的
            }else if(this.type == "shengcheng" || this.type == "editor"){
                if(this.$route.query.number == 1){
                    this._ajax('userHomePage/marketList',{
                    }, msg => {
                        this.list = msg
                        // this._message(1,"销售成本科目成功")
                    })
                }else if(this.$route.query.number == 2){
                    this._ajax('userHomePage/workerList',{
                    }, msg => {
                        this.list = msg
                        // this._message(1,"职工薪酬科目成功")
                    })
                }else if(this.$route.query.number == 3){
                    this._ajax('userHomePage/selectOldTemplate',{
                    }, msg => {
                        this.list = msg
                        // this._message(1,"固定资产折旧科目成功")
                    })
                }else if(this.$route.query.number == 4){
                    this._ajax('userHomePage/selectLongSellTemplate',{
                    }, msg => {
                        this.list = msg
                        // this._message(1,"长期摊销科目成功")
                    })
                }else if(this.$route.query.number == 5){
                    this._ajax('userHomePage/jiTiShuiJinTemplate',{
                    }, msg => {
                        this.list = msg
                        // this._message(1,"计提税金科目成功")
                    })
                }

            }


        },
        methods:{
            change_kemu(val){
                if(this.type1 === 1) {

                    return
                }
                let obj = this.list.find(item => {
                    return val == item.subjects_name + ',' + item.subjects_number
                })
                this.type1 = obj.type1
            },
            // 接受固定资产的确认和取消键
            gudingzichan_jieshou(x){
                if(x == 0){
                    this.gudingzichan_kaiguan = false
                }else{
                    this.$emit("geiguding",x,"ok")
                }
            },
            jiancha(){
                // 判断信息是否填写完整
                let jie_num = 0
                let dai_num = 0

                let error = false
                this.user.tableData.forEach(item=>{
                    // 如果有一项没填的话终止循环
                    if(item.jie == "" && item.dai ==""){
                        error = true
                        return
                    }
                    // 计算总数
                    if(item.jie != ""){
                        jie_num += Number(item.jie)
                    }
                    if(item.dai != ""){
                        dai_num += Number(item.dai)
                    }
                })
                if(error){
                    this._message(2,"请将信息填写完整")
                    return
                }
                this.$refs["user"].validate((valid) => {
                    if (valid) {
                        // 判断是否借贷平衡
                        if(jie_num != dai_num){
                            this._message(2,"借贷不平衡或金额包含非法字符")
                            return
                        }
                        // if(this.type=="add_pingzheng"){
                            console.log(this.type1)
                            if(this.type1 == 1){
                                if(this.heji0 > 5000 || this.heji1 > 5000){
                                    this.type1 = ""
                                    this.gudingzichan_kaiguan = true
                                    this.$emit("geixinxi",this.user,"guding")
                                    return
                                }
                            }
                        // }
                        this.$emit("geixinxi",this.user,"ok")
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            jie_change(e){
                // 找到用户操作的那一项的索引
                let index = this.user.tableData.findIndex(item=>{
                    return item.index == e.index
                })
                if(this.user.tableData[index].jie != ""){
                    this.user.tableData[index].dai_show = true
                }else{
                    this.user.tableData[index].dai_show = false
                }
            },
            dai_change(e){
                let index = this.user.tableData.findIndex(item=>{
                    return item.index == e.index
                })
                if(this.user.tableData[index].dai != ""){
                    this.user.tableData[index].jie_show = true
                }else{
                    this.user.tableData[index].jie_show = false
                }
            },
            add(){
                this.index+=1
                this.user.tableData.push({name:'',zhaiyao:'',jie:'',dai:'',jie_show:false,dai_show:false,index:this.index})
            },
            del(index){
                let i = this.user.tableData.findIndex(item=>{
                    return item.index == index
                })
                this.user.tableData.splice(i,1)
            },
        }
    }
</script>

<style scoped>

</style>


