// import FileSaver from 'file-saver';
// import XLSX from 'xlsx';

export default {
    data() {
        return {
            screenHeight: 880,          //弹出窗口
            sleeveShow: false,

            uuid:'',
            accountId:'',

            liushui_date:"",
            year:"",
            month:"",

            jiezhuan_date:"",
            jiezhuan_year:"",
            jiezhuan_month:""
        }
    },
    created() {
        //获取当前浏览器高度
        this.screenHeight = document.documentElement.clientHeight - 50;
        //绑定onresize事件实时更新窗口高度
        const _this = this;
        window.onresize = function () {
            console.log(_this.screenHeight)
            _this.screenHeight = document.documentElement.clientHeight - 50;
        };
    },
    computed: {

    },
    methods: {
        getStorage(){
            this.uuid = JSON.parse(localStorage.getItem('uuid'))
            this.accountId = JSON.parse(localStorage.getItem('workAccountId'))
        },
        get_liushui_date(){
            // 保存首页的流水id
            this.liushui_date = JSON.parse(sessionStorage.getItem('liushui_date'))
            this.year = JSON.parse(sessionStorage.getItem('liushui_date')).split("-")[0]
            this.month = JSON.parse(sessionStorage.getItem('liushui_date')).split("-")[1]
        },
        get_jiezhuan_date(){
            // 如果结转过
            if(JSON.parse(localStorage.getItem('jiezhuan_date')).includes("-")){
                // 先取年月
                this.jiezhuan_date = JSON.parse(localStorage.getItem('jiezhuan_date'))
                this.jiezhuan_year = JSON.parse(localStorage.getItem('jiezhuan_date')).split("-")[0]
                this.jiezhuan_month = JSON.parse(localStorage.getItem('jiezhuan_date')).split("-")[1]
                // 如果上次结转的是12月
                if(this.jiezhuan_month == 12){
                    this.jiezhuan_month = 1
                    this.jiezhuan_year = Number(this.jiezhuan_year) + 1
                }else{
                    this.jiezhuan_month = Number(this.jiezhuan_month) + 1
                }
                // 月数补0
                this.jiezhuan_month = this.jiezhuan_month < 10 ? '0'+ this.jiezhuan_month: this.jiezhuan_month;
                // 更显年月
                this.jiezhuan_date = `${this.jiezhuan_year}-${this.jiezhuan_month}`
            }else{
                //否则结转日期为账套起始日期
                this.jiezhuan_date = JSON.parse(localStorage.getItem('start_date')).split("-")[0] + '-' + JSON.parse(localStorage.getItem('start_date')).split("-")[1]
                this.jiezhuan_year = JSON.parse(localStorage.getItem('start_date')).split("-")[0]
                this.jiezhuan_month = JSON.parse(localStorage.getItem('start_date')).split("-")[1]
            }
        },

        // 获取当前时间
        transDate(date) {
            var month = date.getMonth() + 1 < 10 ? "0" + (date.getMonth() + 1) : date.getMonth() + 1;
            var currentDate = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
            return date.getFullYear() + "-" + month + "-" + currentDate;
        },
        //图片加载错误提示图;
        imgError(e) {
            e.target.src = './static/error.png'
        },
        //查看大图方法
        magnifier(url) {
            const h = this.$createElement;
            this.$msgbox({
                title: '图片查看',
                message: h('img', {
                    attrs: {
                        src: this.aliurl + url
                    },
                    style: {
                        width: '100%'
                    },
                    on: {
                        error(e) {
                            console.log(e);
                            e.target.src = './static/error.png'
                        }
                    }
                })
            })
        },
        msgChange(data, msg, text) {
            for (let i = 0; i < msg.lengt; i++) {
                if (data == msg[i]) {
                    return text[i];
                }
            }
        }
    },
    components: {
        // FileSaver,
        // XLSX
    }
}



